var data=[{"id":"1","name":"Rat","image":"./images/1_rat.png","years":"1924, 1936, 1948, 1960, 1972, 1984, 1996, 2008, 2020, 2032","description":"Ranking the first in the Chinese zodiac, rat represents wisdom. Personality traits for the people born in the year of the Rat are intelligent, charming, quick-witted, practical, ambitious, and good at economizing as well as social activities. The weaknesses are that the Rats are likely to be timid, stubborn, wordy, greedy, devious, too eager for power and love to gossip."},{"id":"2","name":"Ox","image":"./images/2_ox.png","years":"1925, 1937, 1949, 1961, 1973, 1985, 1997, 2009, 2021, 2033","description":"Ranking the second in the Chinese zodiac, Ox or Cow is the symbol of diligence in Chinese culture. People under the sign of the Ox are usually hard working, honest, creative, ambitious, cautious, patient and handle things steadily. On the negative side, Ox people might be stubborn, narrow-minded, indifferent, prejudiced, slow and not good at communication."},{"id":"3","name":"Tiger","image":"./images/3_tiger.png","years":"1926, 1938, 1950, 1962, 1974, 1986, 1998, 2010, 2022, 2034","description":"Ranking as the third animal in the Chinese zodiac, Tigers are the symbol of brave. People born in the year of the Tiger are friendly, brave, competitive, charming and endowed with good luck and authority. With indomitable fortitude and great confidence, the tiger people can be competent leaders. On the other side, they are likely to be impetuous, irritable, overindulged and love to boast to others."},{"id":"4","name":"Rabbit","image":"./images/4_rabbit.png","years":"1927, 1939, 1951, 1963, 1975, 1987, 1999, 2011, 2023, 2035","description":"Rabbit (Hare) represents longevity, discretion and good luck. It has the fourth position in the Chinese Zodiac. People born under the sign of the Rabbit are kind-hearted, friendly, intelligent, cautious, skillful, gentle, quick and live long. They dislike fighting and like to find solutions through compromise and negotiation. On the negative side, Rabbit people have the potential to be superficial, stubborn, melancholy and overly-discreet."},{"id":"5","name":"Dragon","image":"./images/5_dragon.png","years":"1916, 1928, 1940, 1952, 1964, 1976, 1988, 2000, 2012, 2024","description":"As the symbol of Chinese nation, dragon represents authority and good fortune. It has the fifth position among the Chinese zodiac animals. People born in the year of the Dragon are powerful, kind-hearted, successful, innovative, brave, healthy courageous and enterprising. However, they tend to be conceited, scrutinizing, tactless, quick-tempered and over-confident."},{"id":"6","name":"Snake","image":"./images/6_snake.png","years":"1917, 1929, 1941, 1953, 1965, 1977, 1989, 2001, 2013, 2025","description":"Among the Chinese Zodiac animals, Snake has the sixth position. Snake is regarded to be pliable. Some of the positive characteristics of the people born in the year of the Snake are wise, discreet, agile, attractive and full of sympathy. On the other hand, there is a tendency for them to be lazy, greedy, arrogant and indulging in self-admiration. "},{"id":"7","name":"Horse","image":"./images/7_horse.png","years":"1918, 1930, 1942, 1954, 1966, 1978, 1990, 2002, 2014, 2026","description":"Among the Chinese Zodiac animals, Horse has the sixth position. Horse is regarded to be pliable. Some of the positive characteristics of the people born in the year of the Horse are wise, discreet, agile, attractive and full of sympathy. On the other hand, there is a tendency for them to be lazy, greedy, arrogant and indulging in self-admiration."},{"id":"8","name":"Goat","image":"./images/8_goat.png","years":"1919, 1931, 1943, 1955, 1967, 1979, 1991, 2003, 2015, 2027","description":"Ranking the eighth position of all the animals in Chinese zodiac, Sheep (also Goat or Ram) represents solidarity, harmony and calmness. People born in the year of the Sheep are polite, mild mannered, shy, imaginative, determined and have good taste. On the negative side, they are sometimes pessimistic, unrealistic, short-sighted and slow in behavior."},{"id":"9","name":"Monkey","image":"./images/9_monkey.png","years":"1920, 1932, 1944, 1956, 1968, 1980, 1992, 2004, 2016, 2028","description":" Monkey ranks ninth position in the Chinese Zodiac. They are cheerful and energetic by nature and usually represent flexibility. People under the sign of the Monkey are wise, intelligent, confident, charismatic, loyal, inventive and have leadership. The weaknesses of the Monkeys are being egotistical, arrogant, crafty, restless and snobbish."},{"id":"10","name":"Rooster","image":"./images/10_rooster.png","years":"1921, 1933, 1945, 1957, 1969, 1981, 1993, 2005, 2017, 2029","description":"Rooster (or Chicken) ranks the tenth among the Chinese zodiac animals. In Chinese culture, Rooster represents fidelity and punctuality, for it wakes people up on time. People born in the year of the Rooster are beautiful, kind-hearted, hard-working, courageous, independent, humorous and honest. They like to keep home neat and organized. On the other side, they might be arrogant, self-aggrandizing, persuasive to others and wild as well as admire things or persons blindly."},{"id":"11","name":"Dog","image":"./images/11_dog.png","years":"1922, 1934, 1946, 1958, 1970, 1982, 1994, 2006, 2018, 2030","description":"Ranking as the eleventh animal in Chinese zodiac, Dog is the symbol of loyalty and honesty. People born in the Year of the Dog possess the best traits of human nature. They are honest, friendly, faithful, loyal, smart, straightforward, venerable and have a strong sense of responsibility. On the negative side, they are likely to be self-righteous, cold, terribly stubborn, slippery, critical of others and not good at social activities."},{"id":"12","name":"Pig","image":"./images/12_pig.png","years":"1923, 1935, 1947, 1959, 1971, 1983, 1995, 2007, 2019, 2031","description":"Occupying the last position in 12 Chinese Zodiac animals, Pig is mild and a lucky animal representing carefree fun, good fortune and wealth. Personality traits of the people born under the sign of the Pig are happy, easygoing, honest, trusting, educated, sincere and brave. The possible dark sides the Pig people are stubbornness, naive, over-reliant, self-indulgent, easy to anger and materialistic. They are sometimes regarded as being lazy."}];

